<template>
  <CSidebar
    fixed
    :minimize="minimize"
    :show="show"
    @update:show="(value) => $store.commit('setView', ['sidebarShow', value])"
  >
    <p class="logo">Visionet CMP</p>

    <CRenderFunction flat :content-to-render="$options.nav" />
    <CSidebarMinimizer
      class="d-md-down-none"
      @click.native="$store.commit('setView', ['sidebarMinimize', !minimize])"
    />
  </CSidebar>
</template>

<script>
import nav from "../_nav";

export default {
  name: "TheSidebar",
  nav,
  computed: {
    show() {
      return this.$store.state.sidebarShow;
    },
    minimize() {
      return this.$store.state.sidebarMinimize;
    },
  },
};
</script>

<style>
.logo {
  padding-top: 1.2rem;
  text-align: center;
  font-size: 1.8rem;
  font-family: monospace;
  font-weight: bolder;
}
</style>
